<template>
  <v-form ref="form">
    <v-layout class="align-center">
      <v-text-field
        name="message"
        v-model="newMessage"
        label="New message"
        required
        browser-autocomplete="off"
      ></v-text-field>

      <div>
        <emoji-picker @emoji:picked="handleEmojiPicked" :data="data"/>
      </div>
    </v-layout>
    <p class="red--text accent-4" v-if="errorText">{{ errorText }}</p>

    <v-btn dark color="indigo" @click="createMessage">
      Send
      <v-icon right dark>send</v-icon>
    </v-btn>
  </v-form>
</template>

<script>
import data from "@zaichaopan/emoji-picker/data/emojis.json";

export default {
  name: "NewMessage",
  props: ["name"],
  data() {
    return {
      newMessage: "",
      errorText: null,
      data: data
    };
  },
  methods: {
    createMessage() {
      if (this.newMessage) {
        db.collection("messages")
          .add({
            message: this.newMessage,
            name: this.name,
            timestamp: Date.now()
          })
          .catch(err => {
            console.log(err);
          });
        this.newMessage = null;
        this.errorText = null;
      } else {
        this.errorText = "A message must be entered first!";
      }
    },

    handleEmojiPicked(emoji) {
      this.newMessage = `${this.newMessage} ${emoji}`;
    }
  }
};
</script>

