<template>
  <v-container fill-height>
    <v-layout align-center text-xs-center wrap>
      <v-flex xs12 sm6 offset-sm3>
        <v-toolbar dark color="indigo">
          <v-toolbar-title>Chat</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-icon>message</v-icon>
        </v-toolbar>
        <v-card color="indigo lighten-5">
          <v-list class="pa-0">
            <div class="chatroom" v-chat-scroll>
              <div class="message" v-for="message in messages" :key="message.id">
                <div class="pa-3">
                  <v-list-tile-content ripple class="mr-3">
                    <v-layout
                      align-center
                      justify-space-between
                      row
                      fill-height
                      fluid
                      class="width100"
                    >
                      <div class="title indigo--text">{{ message.name }}</div>

                      <p class="grey--text lighten-4 no-wrap ma-0 caption">{{ message.timestamp }}</p>
                    </v-layout>
                    <div class="pt-3">{{message.message}}</div>
                  </v-list-tile-content>
                </div>
                <v-divider></v-divider>
              </div>
            </div>
          </v-list>
          <v-container>
            <NewMessage :name="name"/>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import NewMessage from "@/components/NewMessage";
import moment from "moment";

export default {
  name: "Chat",
  props: ["name"],
  components: {
    NewMessage
  },
  data: () => ({
    messages: []
  }),
  created() {
    let ref = db.collection("messages").orderBy("timestamp");
    ref.onSnapshot(snapshot => {
      snapshot.docChanges().forEach(change => {
        if ((change.type = "added")) {
          let doc = change.doc;
          this.messages.push({
            id: doc.id,
            name: doc.data().name,
            message: doc.data().message,
            timestamp: moment(doc.data().timestamp).format("LTS")
          });
        }
      });
    });
  }
};
</script>

<style scoped>
.chatroom {
  height: 300px;
  overflow-y: auto;
}

.width100 {
  width: 100%;
}

/* deleted scroll */
.chatroom::-webkit-scrollbar {
  width: 0;
}

.chatroom {
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
</style>
